.header {
	// position: fixed;
	&__logo-svg {
		width: 210px;
		height: 30px;
	}
	&__nav-menu {
		color: #fff;
		font-family: Inter;
		font-size: 20px;
		font-style: normal;
		font-weight: 700;
		line-height: normal;
		letter-spacing: -0.4px;
	}
	&__container {
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		align-items: center;
		max-width: 1440px;
		padding-top: 30px;
		padding-left: 40px;
		padding-right: 40px;
	}
}
.nav-menu {
	&__list {
		align-items: center;
		display: flex;
		column-gap: 20px;
	}
	&__item {
		&_get-access {
			color: #fff;
			padding: 10px 20px;
			background: #eb2828;
		}
	}
}
